span.box, a.box {
	display: inline-block;
	box-sizing: border-box;
	border: 1px solid #b3b3b3;
	background: #e9edf1;
	padding: 0 rem(4);
	line-height: rem(22);
	margin-top: rem(4); }

main {
	margin-right: auto;
	margin-left: auto;
	line-height: 1.5em;
	letter-spacing: 0.04em;
	.description {
		font-size: rem(16);
		color: rgba(255, 0, 29, .7); }
	.red {
		color: rgba(255, 0, 29, .7); }
	section {
		padding-bottom: rem(50);
		width: 100%;
		float: left;
		[class ^= title] {
			text-transform: uppercase;
			font-weight: bold; }
		&>.title {
			padding: rem(120) rem(30) rem(100) rem(30);
			background: #556655;
			color: white; }
		.indent {
			padding-left: rem(40); }
		article {
			box-sizing: border-box;
			max-width: rem(1300);
			width: 100%;
			padding: 0 rem(20);
			font-weight: 500;
			margin: 0 auto;
			&:first-child {
				padding-top: rem(20); }
			padding-top: rem(50);
			.title {
				font-size: rem(35);
				padding-bottom: rem(20); }
			h4.title {
				font-size: rem(25); }
			a {
				color: #48a0ef;
				font-size: rem(20);
				font-weight: bold;
				text-decoration: none;
				&:hover {
					color: #0069c8; } }
			ol {
				list-style-type: decimal;
				padding-left: rem(30);
				margin-bottom: rem(20); }

			ol>li {
				font-size: rem(22);
				padding-top: rem(20);
				&:first-child {
					padding-top: 0; }
				&>ol, &>ul {
					font-weight: 400;
					font-size: rem(20);
					a {
						font-weight: 400; } }
				&>ol {
					list-style-type: decimal-leading-zero; }
				&>ul ul {
					list-style-type: circle; } }
			ul {
				list-style-type: initial;
				padding-left: rem(30); }
			img {
				display: block;
				padding: rem(30) 0;
				margin: 0 auto;
				&.width-500 {
					max-width: rem(500);
					width: 100%; }
				&.width-1000 {
					max-width: rem(1000);
					width: 100%; } }
			.ex {
				font-size: rem(24);
				font-weight: 400;
				color: gray;
				border: 1px solid gray; }
			.pt-n > li, .pt-n {
				padding-top: 0; }
			// ==============================
			// result pannel
			// ==============================
			.result {
				margin-top: rem(20);
				border: rem(2) solid #e9edf1;
				&.bg {
					background: url("../img/ui-kit.jpg") no-repeat; }
				&.center {
					height: 500px;
					background-position: center; }
				&.flud-center {
					padding-bottom: 117.8%;
					background-size: cover; }
				&.bg-pos {
					position: relative;
					height: rem(250);
					overflow: hidden;
					img {
						width: rem(200);
						padding: 0;
						position: absolute;
						left: 50%;
						transform: translateX(-50%); } } }
			// ==============================
			// result pannel END
			// ==============================
			.width-wrap {
				max-height: rem(800);
				overflow: hidden; } }

		article.color-box {
			div {
				line-height: rem(40);
				padding: rem(10);
				margin: rem(10);
				color: white;
				&.black {
					background: black; }
				&.blue {
					background: #48a0ef; }
				&.soldier {
					background: #556655; }
				&.red {
					background: rgba(255, 0, 29, .7); }
				&.e9edf1 {
					background: #e9edf1; } } }
		article.study-list {
			padding-bottom: rem(50);
			border-bottom: rem(3) solid black; } } }

.code {
	box-sizing: border-box;
	padding: rem(10);
	border: rem(3) solid #e9edf1;
	background: #272822;
	color: white;
	font-weight: normal;
	overflow: auto; }
.code-box {
	display: flex;
	margin-top: rem(20);
	overflow-x: auto;
	border: rem(1) solid #e9edf1;
	background: #272822;
	xmp {
		border: 0;
		border-right: rem(1) solid #e9edf1;
		width: 50%;
		&:last-child {
			border: 0; }
		overflow: initial; } }
// ==============================
// day task
// ==============================
main {
	section {
		display: none; }
	.typography {
		display: block; } }
