@media screen and (min-width : 1000px) {
	main section article img.width-500 {
		margin: 0;
		max-width: rem(700); } }
@media screen and (max-width : 730px) {
	html {
		font-size: 30px; } }
@media screen and (max-width : 450px) {
	html {
		font-size: 25px; } }
@media screen and (max-width : 325px) {
	xmp {
		font-size: rem(20); } }
